<template>
    <v-container>
        <v-card>
            <v-container grid-list-md mb-0>
                <h2 class="text-md-center">User Request List</h2>
                 <v-layout row wrap style="margin:10px">
                    <v-flex xs6>
                        <v-btn depressed rounded style="text-transform: none !important;" color="red accent-3"
                            @click="pdf()" class="ml-5">
                            <v-icon size="18" class="mr-2">mdi-file-pdf</v-icon>
                            Convert to PDF
                        </v-btn>
                    </v-flex>
                    <v-flex xs6 class="text-right">
                        <v-text-field v-model="keyword" append-icon="mdi-magnify" label="Search" hide-details>
                        </v-text-field>
                    </v-flex>
                </v-layout>

                <v-data-table :headers="headers" :items="requests" :search="keyword" :loading="load">
                    <template v-slot:body="{ items }">
                        <tbody>
                            <tr v-for="(item,index) in items" :key="item.id_wish">
                                <td>{{ index + 1 }}</td>
                                <td>{{ item.user_id }}</td>
                                <td>{{ item.title }}</td>
                                <td>{{ item.authors}}</td>
                                <td>{{ item.publisher }}</td>
                                <td>{{ item.category }}</td>
                                <td class="text-center">
                                    <v-btn icon color="green" light @click="deleteData(item.id_wish)">
                                        <v-icon>mdi-book-plus</v-icon>
                                    </v-btn>
                                </td>
                            </tr>
                        </tbody>
                    </template>
                </v-data-table>
            </v-container>
        </v-card>
        <v-snackbar v-model="snackbar" :color="color" :multi-line="true" :timeout="3000">
            {{ text }}
            <v-btn dark text @click="snackbar = false">
                Close
            </v-btn>
        </v-snackbar>
    </v-container>
</template>
<script> 

export default {    
    data () {       
        return {         
            dialog: false,         
            keyword: '',         
            headers: [             
                {               
                    text: 'No',               
                    value: 'no',             
                },  
                {               
                    text: 'User_ID',               
                    value: 'user_id',             
                },             
                {               
                    text: 'Book Title',               
                    value: 'title'             
                },             
                {               
                    text: 'Authors',               
                    value: 'authors'             
                },             
                {               
                    text: 'Publisher',               
                    value: 'publisher'             
                },
                {               
                    text: 'Category',               
                    value: 'category'             
                },          
                {               
                    text: 'Aksi',
                    value: null,
                    sortable: false
                },         
            ],         
            requests: [],         
            snackbar: false,          
            color: null,         
            text: '',          
            load: false, 
            form: {            
                title : '',           
                authors : '',           
                publisher : '',
                category : ''        
            },         
            request : new FormData,         
            typeInput: 'new',        
            errors : '',         
            updatedId : '',       
        }     
    },     
    methods:{         
        getData(){             
            var uri = this.$apiUrl + '/requestlist'             
            this.$http.get(uri).then(response =>{                 
                this.requests=response.data.message             
            })               
        }, 
                
        sendData(){
            this.request.append('title', this.form.title);      
            this.request.append('authors', this.form.authors);       
            this.request.append('publisher', this.form.publisher);
            this.request.append('category', this.form.category);
            this.request.append('user_id', this.$session.get('user_id'));
            var uri =this.$apiUrl + '/requestlist'             
            this.load = true             
            this.$http.post(uri,this.request).then(response =>{               
                this.snackbar = true; //mengaktifkan snackbar               
                this.color = 'green'; //memberi warna snackbar               
                this.text = response.data.message; //memasukkan pesan ke snackbar               
                this.load = false;               
                this.dialog = false               
                this.getData(); //mengambil data user               
                this.resetForm();           
            }).catch(error =>{               
                this.errors = error               
                this.snackbar = true;               
                this.text = 'Try Again';               
                this.color = 'red';               
                this.load = false;           
            })         
        }, 

        updateData(){             
            this.request.append('title', this.form.title);             
            this.request.append('authors', this.form.authors);             
            this.request.append('publisher', this.form.publisher);
            this.request.append('category', this.form.category);
            this.request.append('user_id', this.$session.get('user_id'));
            var uri = this.$apiUrl + '/requestlist/' + this.updatedId;             
            this.load = true             
            this.$http.post(uri,this.request).then(response =>{ 
                this.snackbar = true; //mengaktifkan snackbar               
                this.color = 'green'; //memberi warna snackbar               
                this.text = response.data.message; //memasukkan pesan ke snackbar               
                this.load = false;               
                this.dialog = false               
                this.getData(); //mengambil data user               
                this.resetForm();               
                this.typeInput = 'new';           
            }).catch(error =>{               
                this.errors = error               
                this.snackbar = true;               
                this.text = 'Try Again';               
                this.color = 'red';               
                this.load = false;               
                this.typeInput = 'new';           
            })         
        },    

        editHandler(item){           
            this.typeInput = 'edit';           
            this.dialog = true;           
            this.form.title = item.title;           
            this.form.authors = item.authors;           
            this.form.publisher = item.publisher,
            this.form.category = item.category,        
            this.updatedId = item.id_wish
        },
        
        deleteData(deleteId) { //mengahapus data             
            var uri = this.$apiUrl + '/requestlist/' + deleteId; //data dihapus berdasarkan id_wish
            this.$http.delete(uri).then(response =>{                 
                this.snackbar = true;                 
                this.text = response.data.message;                 
                this.color = 'green'                 
                this.deleteDialog = false;                 
                this.getData();             
            }).catch(error =>{                 
                this.errors = error                 
                this.snackbar = true;                 
                this.text = 'Try Again';                 
                this.color = 'red'; 
            })         
        },
        
        setForm(){             
            if (this.typeInput === 'new') {               
                this.sendData()             
            } else {
                this.updateData()             
            }         
        },    

        resetForm(){             
            this.form = {               
                title : '',               
                authors : '',               
                publisher : '',
                category : ''     
            }         
        },
        
        pdf(){
            var uri = this.$apiUrl + '/requestlistpdf';
            window.open(uri);
        }
    },     
    mounted(){         
        this.getData();     
        }, 
    } 
</script> 
<style lang="scss" scoped>
.v-btn {
  color : white;
}
</style>